* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color:  #63C7B2;
  font-family: 'Rubik', sans-serif;
}

p {
  text-align: center;
  font-size: 1.25em;
}

.drum-pad {
  background-color: #263D42;
  color: #CCDBDC;
  width: 90%;
  font-size: 3em;
  text-align: center;
  border: none;
  margin: 5% 5% 0;
  padding: 5%;
  cursor: pointer;
  outline: none;
}

.drum-pad:hover {
  background-color: #1E3135;
}

.drum-pad:active, .keyPressed {
  background-color: #1E3135;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 1px;
  grid-row-gap: 1px;
  width: 80%;
  margin: 0 0 2.5% 10%;
}

